import React from 'react';
import './App.css';
import Particle from './component/particle/particle'
import {Layout, Header,Navigation,Drawer,Content} from 'react-mdl';
import Footer from './component/footer/footer';
import {BrowserRouter as Router, Route, Link, Switch} from 'react-router-dom';
import Landing from './pages/landingpage/landingpage';
import AboutMe from './pages/aboutme/aboutme';
import Project from './pages/project/project';
import Contact from './pages/contact/contact';
import Resume from './pages/resume/resume';
import {toggleNav2, toggleNav3} from "./script";



function App() {
  
  return (
    <div className="mainDiv">
    <Router basename={ process.env.PUBLIC_URL + '/' }>
    <Layout fixedHeader>
    <nav className="navbar navbar-expand-lg navbar-light">
      <button style={{zIndex: "10000"}} onClick={toggleNav2} className="navbar-toggler ml-auto" type="button">
        <span className="line"></span> 
        <span className="line"></span> 
        <span className="line"></span>
      </button>
      <div className="navbar-collapse" style={{textAlign: "right"}} id="nav1">
        <div className="navbar-nav ml-auto">
            <Link onClick={toggleNav3} className="nav-item nav-link text-white hell1" to="/">Profile</Link>
            <Link onClick={toggleNav3} className="nav-item nav-link text-white hell2" to="/aboutMe">About</Link>
            <Link onClick={toggleNav3} className="nav-item nav-link text-white hell3" to="/resume">Resume</Link>
            <Link onClick={toggleNav3} className="nav-item nav-link text-white hell4" to="/project">Project</Link>
            <Link onClick={toggleNav3} className="nav-item nav-link text-white hell5" to="/contact">Contact</Link>
        </div>
      </div>
    </nav>
        <Content style={{overflowY: "hidden"}} >
          <div className="page-content" />
          <Particle />
          <Switch> 
              <Route exact path= '/' component={Landing} />
              <Route path= '/aboutMe' component={AboutMe} />
              <Route path= '/contact' component={Contact} />
              <Route path= '/project' component={Project} />
              <Route path= '/resume' component={Resume} />
              <Route path= '/landing' component={Landing} />
            </Switch> 
        </Content>
        <Footer/>
    </Layout>
    </Router>
</div>
  );
}

export default App;
